import { VulnerabilitySeverity } from 'src/resources/TagResource';
export function getSeverityColor(severity) {
    switch (severity) {
        case VulnerabilitySeverity.Critical:
            return '#7D1007';
        case VulnerabilitySeverity.High:
            return '#C9190B';
        case VulnerabilitySeverity.Medium:
            return '#EC7A08';
        case VulnerabilitySeverity.Low:
            return '#F0AB00';
        case VulnerabilitySeverity.None:
            return '#3E8635';
        default:
            return '#8A8D90';
    }
}
export function formatDate(date) {
    if (date == -1) {
        return 'N/A';
    }
    var adjustedDate = typeof date === 'number' ? date * 1000 : date;
    return new Date(adjustedDate).toLocaleString('en-US', {
        timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
        timeStyle: 'short',
        dateStyle: 'medium',
    });
}
export function formatSize(sizeInBytes) {
    if (!sizeInBytes) {
        // null or undefined
        return 'N/A';
    }
    var i = Math.floor(Math.log(sizeInBytes) / Math.log(1024));
    return ((sizeInBytes / Math.pow(1024, i)).toFixed(2) * 1 +
        ' ' +
        ['B', 'kB', 'MB', 'GB', 'TB'][i]);
}
export function isValidEmail(email) {
    var regex = /\S+@\S+\.\S+/;
    return regex.test(email);
}
